import navigation from "../util/navigation";
export default {
    name: "BottomBar",
    props: {
        category: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            cuIcons: null
        }
    },
    created() {
      this.cuIcons = navigation;
    },
    methods: {
        changeCategory(category) {
            this.$emit('changeCategory', category);
        }
    }
